import { useQuery } from "@tanstack/react-query";
import { fetchCoinChart } from "./api";
import ApexCharts from "react-apexcharts"; //  chart      
import styled from "styled-components";

const ChartDiv = styled.div`
  margin-top: 15px;
`;

interface IChartProps {
  coinId: string;
}

interface IHistoricalData {
  time_open: number;
  time_close: number;
  open: number;
  high: number;
  low: number;
  close: number;
  volume: number;
  market_cap: number;
}

function Ca({ coinId }: IChartProps) {
  const { isLoading, data } = useQuery<IHistoricalData[]>({
    queryKey: ["ohlcv", coinId],
    queryFn: () => fetchCoinChart(coinId),
  });

  const ms = data?.map((price) => new Date(price.time_open * 1000)); //  

  // map   
  const date = ms?.map((date) => {
    const month = date.getMonth() + 1; // getMonth() 0  +1 
    const day = date.getDate();
    const formattedDate = ` ${month} ${day}`;

    return formattedDate;
  });

  return (
    <ChartDiv>
      <ApexCharts
        type="line"
        width="500"
        series={[
          {
            name: "price",
            data: data ? data?.map((price) => price.close) : [], // data     []
          },
        ]}
        options={{
          theme: {
            mode: "dark",
          },
          xaxis: {
            categories: date,
          },
        }}
      />
    </ChartDiv>
  );
}

export default Ca;
